import React, {useEffect, useState} from 'react';
import axios from '../../utils/axios';
import { Link } from 'react-router-dom';

const Home = (props) => {
    const [state, setState] = useState({
        botState: 'inactive',
        trades: []
    });
    const [loading, setLoading] = useState(true);
    const [botLoading, setBotLoading] = useState(false);

    const toggleBot = (e) => {
        e.preventDefault()
        setBotLoading(true);
        axios('/toggle', {
            headers: {
                "X-Auth-token": localStorage.getItem('X-Auth-token')
            }
        })
        .then(response => {
            setBotLoading(false);
            let data = response.data;
            if(data.status == 401) {
                alert('You need to log in');
                return;
            }
            if(data.status == 200) {
                return updateDetails();
            }
            alert(data.message);
            
        }).catch(err => {
            alert('Something went wrong, try again');
            setBotLoading(false);
        });
    }

    const updateDetails = () => {
        axios('/details', {
            headers: {
                "X-Auth-token": localStorage.getItem('X-Auth-token')
            }
        })
        .then(response => {
            let data = response.data;
            if(data.status == 401) {
                alert('You need to log in');
                props.history.push('/login');
            }
            if(data.status == 200) {
                setLoading(false);
                setBotLoading(false);
                setState({...state, botState: data.alive ? 'active': 'inactive', trades: data.trades});
            }
        })
        .catch(err => {
            alert
        (err.message || 'something went wrong');            
        })
    }

    useEffect(() => {
        updateDetails();
    }, []);
    return (
        <div id="page-wrapper" >
            <div id="page-inner">
                {
                    loading? <div style={{margin: '30px auto'}}>Loading...</div>:
                    <>
                        <div className="row">
                            <div className="col-lg-12">
                            <h2>DASHBOARD</h2>   
                            </div>
                        </div>              
                        
                        <hr />
                        <div className="row">
                            <div className="col-lg-12 ">
                                <div className="alert alert-info">
                                    <strong>Welcome Paul!... </strong> Bot status: 
                                    <span className={state.botState == 'active'? 'text-success': 'text-danger'}>
                                        {
                                            state.botState == 'active'? ' Active (running)':
                                            ' Inactive (dead)'
                                        }
                                    </span>.
                                    <p>Number of completed trades today: {state.trades.length}</p>
                                </div>
                            
                            </div>
                        </div>
                        
                        <div className="row text-center pad-top">
                            <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6">
                                <div className="div-square">
                                    {
                                        botLoading? <><div class="lds-ripple"><div></div><div></div></div></>:
                                        <Link onClick={e => toggleBot(e)} to='#' className='disabled'>
                                            <i className={`fa fa-power-off fa-5x text-${state.botState == 'active'? 'danger': 'success'}`}></i>
                                            <h4>
                                                {
                                                    state.botState == 'active'? ' Power off':
                                                    ' Power On'
                                                }
                                            </h4>
                                        </Link>
                                    }
                                    
                                </div>
                            </div> 

                            <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6">
                                <div className="div-square">
                                    <Link to="/preferences" >
                                        <i className="fa fa-gear fa-5x text-info"></i>
                                        <h4>Preferences</h4>
                                    </Link>
                                </div>
                            </div>

                            <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6">
                                <div className="div-square">
                                    <Link to="/logs" >
                                        <i className="fa fa-clipboard fa-5x text-success"></i>
                                        <h4>Logs</h4>
                                    </Link>
                                </div>
                        </div>
                            
                            <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6">
                                <div className="div-square">
                                    <Link to="/password" >
                                        <i className="fa fa-key fa-5x text-warning"></i>
                                        <h4>Password</h4>
                                    </Link>
                                </div>
                            </div>
                        </div>
                            
                        <div className="row">
                            <div className="col-lg-12 ">
                            <br />
                                <div className="alert alert-danger">
                                    <strong>Warning: </strong> This bot is not guaranteed to operate with 100% accuracy.
                                </div>
                            
                            </div>
                        </div>
                    </>
                }
                
            </div>
        </div>
    );
};

export default Home;